.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  font-family: Helvetica, Arial , sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  flex: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  background-color: rgb(86, 115, 248);
  width: 100%;
}

.container {
  display: flex;
  flex-direction:column;
}

.container__input {
  font-size: 1em;
  border: 1px solid gray;
  width: 200px;
  height: 40px;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  padding-left: 8px;
}

.header__btn {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1em;
  width: 90px;
  height: 87px;
  border: none;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  padding-left: 5px;
}

.todo-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  flex: 75%;
  width: 100%;
}

.task {
  width: 500px;
  height: 50px;
  display: flex;
  color: #fff;
  margin: 15px;
}

.content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content span {
  font-family: Helvetica, Arial, sans-serif;
  display: grid;
  place-items: center;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: rgb(86, 115, 248);
}

.content__btn {
  background-color: tomato;
  color: #fff;
  flex: 20%;
  height: 100%;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1em;
  border: none;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  padding-left: 5px;
}

